/* 混入对象 */

// 表格内容区域自动大小
export const tableContentAutoResize = {
    data () {
        return {
          tableBoxHeight_: 10,
        }
    },
    methods: {
        _tableContentAutoResize () {
            if (!this.$refs.tableBox) return
            setTimeout(() => {
                this.tableBoxHeight_ = this.$refs.tableBox.clientHeight
            }, 200)
            let timeout = 0
            window.onresize = () => {
                if (timeout) clearTimeout(timeout)
                this.tableBoxHeight_ = 0
                timeout = setTimeout(() => {
                    this.tableBoxHeight_ = this.$refs.tableBox.clientHeight
                }, 200)
            }
        }
    },
    mounted () {
        setTimeout(() => {
            this._tableContentAutoResize()
        }, 200)
    },
    beforeDestroy () {
        window.onresize = () => {}
    }
}